import React, { Component, createRef } from "react";
import { Link } from "react-router-dom";
import robotImg from "../../assets/animation1.webp";
import "../../style/HomeSection1.css";

class MobileHomeSection1 extends Component {
  constructor(props) {
    super(props);

    this.gif1 = robotImg;
    this.imgRef1 = createRef();
  }
  render() {
    if (this.imgRef1.current) {
      this.imgRef1.current.src = this.gif1;
    }

    return (
      <section className="section-1">
        <img
          src={this.gif1}
          alt="Robot"
          className="robot-img"
          ref={this.imgRef1}
        />
        <div>
          <div className="header">Don't make them think.</div>
          <div className="context">
            Its a magical piece of glass. Its so fast most PC laptops cant
            catch up. It has pro cameras that can transform reality. And you can
            use it with touch, pencil, keyboard and now trackpad. Its the new
            iPad Pro.
          </div>

          <Link to="/" className="link">
            Watch the film
          </Link>
        </div>
      </section>
    );
  }
}

export default MobileHomeSection1;
